import React from 'react';
import { Text, Title as Heading, Subheading, Paragraph, Caption } from './paper'
import { useScreenSize } from './Layout';
const Title = ({ children }) => {
    const { width } = useScreenSize()
    return (
        <Text style={{ fontSize: width > 450 ? 60 : 30, marginTop: 10 }}>
            {children}
        </Text>
    )
}

export {
    Text,
    Paragraph,
    Caption,
    Title, Heading, Subheading,
}